@media (width < 350px) {
    .dis_block{
        padding: 40px 0 16px 0;
        min-height: 148px;
        border-radius: 5px;
        box-shadow: 2px 2px 2px 2px rgba(0, 0, 0, 0.2);
        display: block;
        flex-direction: column;
        justify-content: center;
        max-width: 260px;
        margin: 0 auto;
    }
    .dis_block > div{
        margin-top: 40px;
        margin-left: 12px
    }
}

@media (width < 520px) {
    .dis_block{
        padding: 40px 10px 16px 10px;
        min-height: 148px;
        border-radius: 5px;
        box-shadow: 2px 2px 2px 2px rgba(0, 0, 0, 0.2);
        display: block;
        flex-direction: column;
        justify-content: center;
        max-width: 330px;
        margin: 0 auto;
    }
    .dis_block .dis_children{
        margin-top: 40px;
        margin-left: 12px
    }
}

@media (width >= 520px){
    .dis_block{
        padding: 24px 24px 24px 24px;
    }

    .dis_children{
        margin-left: 24px;
        float: left;
    }

}

.dis_block {
    min-height: 148px;
    border-radius: 5px;
    box-shadow: 2px 2px 2px 2px rgba(0, 0, 0, 0.2);
    margin: 30px auto;
    display: flex;
    align-items: center;
}

.dis_block>p {
    margin-bottom: 0px;
}

::-webkit-scrollbar {
    width: 8px;
    height: 8px;
    background-color: none;
}

::-webkit-scrollbar-thumb {
    border-radius: 8px;
    background-image: linear-gradient(rgba(254, 109, 100, 0.5), rgba(224, 25, 20, 0.5))
}

::-webkit-scrollbar-track {
    background-color: none;
}